import { isEqual } from 'lodash-es'

export interface QueryParams {
  [key: string]: string
}

export interface Hash {
  section?: string
  query: QueryParams
}

export type HashChangedCallback = (hash: Hash, prevHash: Hash) => void
export type QueryParamChangedCallback = (value: string) => void
export type SectionChangedCallback = (section: string | undefined) => void

/**
 * This class contains methods to manipulate the hash of the web page
 */
export function createHash() {
  function getHash() {
    return parseHash(window.location.hash.substring(1))
  }

  function setHash(hash: Hash) {
    const hashStr = stringifyHash(hash)
    window.location.hash = hashStr.length ? '#' + hashStr : ''
  }

  function setQueryParam(key: string, value: string) {
    const hash = getHash()
    const changed = hash.query[key] !== value
    if (changed) {
      setHash({
        ...hash,
        query: {
          ...hash.query,
          [key]: value
        }
      })
    }
  }

  function getQueryParam(key: string): string {
    const hash = getHash()
    return hash.query[key]
  }

  function setQueryParams(query: QueryParams) {
    const hash = getHash()
    const changed = !isEqual(hash.query, query)
    if (changed) {
      setHash({
        ...hash,
        query
      })
    }
  }

  function getQueryParams(): QueryParams {
    const hash = getHash()
    return hash.query
  }

  function setSection(section: string | undefined) {
    const hash = getHash()
    const changed = hash.section !== section
    if (changed) {
      setHash({
        ...hash,
        section
      })
    }
  }

  function getSection(): string | undefined {
    const hash = getHash()
    return hash.section
  }

  function removeQueryParam(key: string) {
    const hash = getHash()
    if (hash.query[key]) {
      delete hash.query[key]
      setHash(hash)
    }
  }

  const listeners: Array<HashChangedCallback> = []

  let prevHash = getHash()
  function handleHashChange() {
    const hash = getHash()

    listeners.forEach((callback) => {
      try {
        callback(hash, prevHash)
      } catch (e) {
        console.error(e)
      }
    })

    prevHash = hash
  }

  function onChangeHash(callback: HashChangedCallback) {
    if (listeners.length === 0) {
      window.addEventListener('hashchange', handleHashChange)
    }

    listeners.push(callback)
  }

  function onChangeQueryParam(key: string, callback: QueryParamChangedCallback) {
    onChangeHash((hash, prevHash) => {
      const changed = hash.query[key] !== prevHash.query[key]
      if (changed) {
        callback(hash.query[key])
      }
    })
  }

  function onChangeSection(callback: SectionChangedCallback) {
    onChangeHash((hash, prevHash) => {
      const changed = hash.section !== prevHash.section
      if (changed) {
        callback(hash.section)
      }
    })
  }

  function destroy() {
    window.removeEventListener('hashchange', handleHashChange)
  }

  return {
    getHash,
    setHash,
    getQueryParam,
    setQueryParam,
    getQueryParams,
    setQueryParams,
    getSection,
    setSection,
    onChangeHash,
    onChangeQueryParam,
    onChangeSection,
    removeQueryParam,
    destroy
  }
}

export function parseQueryParams(params: string): QueryParams {
  const pairs = params
    .split('&')
    .filter((pair) => pair.length > 0)
    .map((pair) => pair.split('=').map(decodeURIComponent))

  return Object.fromEntries(pairs)
}

export function stringifyQueryParams(parameters: QueryParams): string {
  return Object.entries(parameters)
    .map((pair) => pair.map(encodeURIComponent).join('='))
    .join('&')
}

export function parseHash(hashStr: string): Hash {
  const hasSection = hashStr.startsWith('/')
  if (hasSection || hashStr.includes('?')) {
    const [section, params] = hashStr.split('?')
    const query = params ? parseQueryParams(params) : {}
    return hasSection ? { section, query } : { query }
  } else {
    return {
      query: parseQueryParams(hashStr)
    }
  }
}

export function stringifyHash(hash: Hash): string {
  const params = stringifyQueryParams(hash.query)

  if (hash.section && hash.section.startsWith('/')) {
    return params ? `${hash.section}?${params}` : hash.section
  } else {
    return params
  }
}
