export async function detectAdBlocker(): Promise<boolean> {
  try {
    await fetch('https://pagead2.googlesyndication.com/pagead/js/adsbygoogle.js', {
      method: 'HEAD',
      mode: 'no-cors'
    })

    // fetching a "suspicious" ad script worked -> no ad blocker active
    return false
  } catch (err) {
    // fetching a "suspicious" ad script failed. Could be an ad-blocker or broken internet

    try {
      const response = await fetch(location.href, { method: 'HEAD', mode: 'no-cors' })

      // if the response is ok, fetching a non-suspicious request was a success -> so, we have an ad-blocker active
      return response.ok
    } catch (err2) {
      return false
    }
  }
}
